import React from "react";
import styles from "./SoundHealing.module.scss";
import OrangeCheck from "../../UI/OrangeCheck.tsx/OrangeCheck";
import { motion } from "framer-motion";
const SoundHealing = () => {
  const xPlusAnimation = {
    hidden: {
      x: -200,
      opacity: 0,
    },
    visible: (custom: number) => ({
      x: 0,
      opacity: 1,
      transition: { delay: custom * 0.6, duration: 0.6 },
    }),
  };
  const xMinusAnimation = {
    hidden: {
      x: 200,
      opacity: 0,
    },
    visible: (custom: number) => ({
      x: 0,
      opacity: 1,
      transition: { delay: custom * 0.6, duration: 0.6 },
    }),
  };

  return (
    <div className={styles.bigWrapper}>
      <div className={styles.shadow}>
        <motion.div
          initial="hidden"
          whileInView="visible"
          viewport={{ amount: 0.5, once: true }}
          className={styles.wrapper}
        >
          <div className={styles.textBlocks}>
            <motion.div
              variants={xPlusAnimation}
              custom={1}
              className={styles.textBlock}
            >
              <h3 className={styles.header}> </h3>
              <p className={styles.text}>
                 -   ,   
                      . 
                      
                ,      
                 .
              </p>
            </motion.div>
            <motion.div
              variants={xMinusAnimation}
              custom={2}
              className={styles.textBlockRight}
            >
              <div className={styles.child}>
                <h3 className={styles.header}>  ?</h3>
                <p className={styles.text}>
                     ,     
                     .    
                  ,       
                  .    , 
                    ,    
                         
                  .
                </p>
              </div>
            </motion.div>
          </div>
          <motion.ul
            variants={xPlusAnimation}
            custom={3}
            className={styles.soundHealingUl}
          >
            <h3 className={styles.header}> </h3>
            <li>
              <OrangeCheck />
              <p className={styles.text}>
                   .   
                     ,     
                    .
              </p>
            </li>
            <li>
              <OrangeCheck />
              <p className={styles.text}>
                   .   
                        
                .
              </p>
            </li>
            <li>
              <OrangeCheck />
              <p className={styles.text}>
                 .    
                     ,    
                      .
              </p>
            </li>
            <li>
              {" "}
              <OrangeCheck />
              <p className={styles.text}>
                 .      
                ,        
                   .
              </p>
            </li>
          </motion.ul>
        </motion.div>
      </div>
    </div>
  );
};

export default SoundHealing;
