import React from "react";
import styles from "./Main.module.scss";
import OrangeButton from "../../UI/OrangeButton/OrangeButton";
import { motion } from "framer-motion";
import { imgUrl } from "../../utils/consts";

interface iMainProps {
  refToAbout: () => void;
  setActiveModal: (activeModal: boolean) => void;
}
const Main: React.FC<iMainProps> = ({ refToAbout, setActiveModal }) => {
  const cardsAnimationY = {
    hidden: {
      y: 200,
      opacity: 0,
    },
    visible: (custom: number) => ({
      y: 0,
      opacity: 1,

      transition: {
        delay: custom * 0.6,
        duration: 0.6,
      },
    }),
  };
  return (
    <motion.div className={styles.wrapper} initial="hidden" animate="visible">
      <div className={styles.main}>
        <motion.h1
          variants={cardsAnimationY}
          custom={1}
          className={styles.header}
        >
             
        </motion.h1>
        <motion.h4
          variants={cardsAnimationY}
          custom={2}
          className={styles.smallHeader}
        >
               World Sound Heal  
             .        
              !
        </motion.h4>
        <motion.div variants={cardsAnimationY} custom={3}>
          <OrangeButton
            onClick={() => setActiveModal(true)}
            text=" "
          />
        </motion.div>
        <img
          onClick={refToAbout}
          className={styles.arrowDown}
          src={
            imgUrl
              ? imgUrl + "/images/icons/arrowDown.svg"
              : "/images/icons/arrowDown.svg"
          }
          alt=""
        ></img>
      </div>
    </motion.div>
  );
};

export default Main;
